//- Basics

.body--index {
	grid-gap: 0;

	.main>article {
		grid-column: full-start / full-end;
	}

	@media screen and (max-width: $breakpoint-sm) {
		hr {
			grid-column: content-start / full-end !important;
		}
	}
}

.main-heading {
	margin-top: var(--gutter);
	margin-bottom: var(--gutter);
	margin: 0;
}

.sticky-heading {
	margin: 0;
	position: -webkit-sticky;
	position: sticky;
	top: calc(var(--header-height));
	transition: transform 0.2s ease-out;

	@media screen and (max-width: $breakpoint-sm) {
		position: static;
	}
}

hr {
	height: 2px;
	width: 100%;
	margin: var(--gutter) 0;
	background: $gradient;
	border: none;
}

.hr--invisible {
	background: transparent;
}

p,
ul,
ol {
	color: var(--text-secondary);
}

//- Blocks

//-- Intro

.intro--index {
	@include block-layout-reset;
	grid-column: full-start / full-end;
	padding: var(--gutter) 0;

	.intro__content {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-column: wide-start / wide-end;

		@media screen and (max-width: $breakpoint-md) {
			grid-template-columns: 1fr 1fr;
		}
	}

	.main-heading {
		display: inline-flex;
	}

	.intro__heading {
		//color     : var(--text-primary);
		//background: linear-gradient(0.35turn, var(--clr-primary), var(--clr-primary) 20%, var(--clr-secondary));;
		@include gradient;
		@include gradient-stuff;
		line-height: 1.5;
		grid-column: 1 / -1;
		margin-right: 0.25ch;
	}

	.intro__text {
		color: var(--text-secondary) !important;
		grid-column: 1 / span 2;
		//font-weight: normal;
	}
}

header,
.intro {
	background-color: var(--card-bg);
	//text-align    : center;
}

.flipper {
	--duration: 6s;
	$interval: 10%;

	display: inline-grid;
	grid-template-columns: [left] 1fr [right];
	grid-template-rows: [top] 1fr [bottom];

	&__word {
		grid-column: left / right;
		grid-row: top / bottom;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
		@include shadify(wb);
		color: var(--text-primary);
		border-radius: 8px;
		padding: 0 8px;
		@include gradient;
		animation-duration: var(--duration);
		animation-iteration-count: infinite;
		animation-timing-function: cubic-bezier(1, 0, 0, 1.65);
		position: relative;
		z-index: 0;

		&:nth-child(1) {
			//transform   : scaleY(1);
			animation-name: word-flip-first;
			z-index: 1;
		}

		&:nth-child(2) {
			//display     : none;
			animation-name: word-flip-second;
			z-index: 2;
		}

		&:nth-child(3) {
			//display     : none;
			animation-name: word-flip-third;
			z-index: 3;
		}
	}

	@keyframes word-flip-first {
		0% {
			transform: scaleY(1);
		}

		#{$interval*3} {
			transform: scaleY(1);
		}

		#{$interval*4} {
			transform: scaleY(0);
		}

		#{$interval*9} {
			transform: scaleY(0);
		}

		100% {
			transform: scaleY(1);
		}
	}

	@keyframes word-flip-second {
		0% {
			transform: scaleY(0);
		}

		#{$interval*3} {
			transform: scaleY(0);
		}

		#{$interval*4} {
			transform: scaleY(1);
		}

		#{$interval*6} {
			transform: scaleY(1);
		}

		#{$interval*7} {
			transform: scaleY(0);
		}

		100% {
			transform: scaleY(0);
		}
	}

	@keyframes word-flip-third {
		0% {
			transform: scaleY(0);
		}

		#{$interval*6} {
			transform: scaleY(0);
			z-index: 3;
		}

		#{$interval*7} {
			transform: scaleY(1);
			z-index: 3;
		}

		#{$interval*9} {
			transform: scaleY(1);
			z-index: 0;
		}

		100% {
			transform: scaleY(0);
			z-index: 0;
		}
	}
}

//-- Articles

//--- Courses

.courses {
	@include block-layout-reset;

	&__heading {
		grid-column: wide-start / content-end;
	}

	&__text {
		grid-column: wide-start / middle;
	}

	&-cards {
		grid-column: wide-start / wide-end;
	}
}

//--- Tutorials

.tutorials {
	@include block-layout-reset;

	hr {
		grid-column: full-start / content-end;
	}

	&__heading {
		grid-row: 2;
		position: relative;
	}
}

//--- Blog

.blog {
	@include block-layout-reset;
	//grid-column: wide-start / full-end;

	/* > * {
        grid-column: wide-start / content-end;
    } */

	hr {
		grid-column: content-start / full-end;
	}

	&__content {
		display: grid;
		grid-template-columns: 450px 1fr;
		gap: var(--base-2);

		@media screen and (max-width: $breakpoint-lg) {
			grid-template-columns: 360px 1fr;
		}

		@media screen and (max-width: $breakpoint-sm) {
			grid-template-columns: 1fr;
		}
	}

	&__heading {
		//grid-column: wide-start / middle;
		position: relative;

		&::after {
			content: "";
			position: absolute;
			right: var(--gutter);
			top: 0;
			bottom: 0;
			width: 2px;
			background-color: var(--divider);

			@media screen and (max-width: $breakpoint-sm) {
				display: none;
			}
		}
	}

	.article-previews {
		display: grid;
		//grid-column: middle / wide-end;
		grid-row-gap: var(--base-4);
	}

	&__link {
		//grid-column    : wide-start / content-end;
		margin-top: var(--article-gap);
		grid-column-start: 2;

		@media screen and (max-width: $breakpoint-sm) {
			grid-column-start: auto;
		}
	}
}

//--- Contact

.contact {
	@include block-layout-reset;
	//grid-column: full-start / wide-end;

	>* {
		grid-column: wide-start / middle;
	}

	hr {
		grid-column: full-start / content-end;
	}

	/* p {
        max-width: 40ch;
    } */
}

.contact-form {
	--radius: 20px;
	display: grid;
	justify-content: center;
	grid-gap: var(--base);
	grid-template-columns: minmax(0, 400px);
	background-color: var(--card-bg);
	box-shadow: var(--card-shadow);
	border-radius: var(--radius);
	@include fluidify(padding, 12px, 72px);

	textarea {
		resize: vertical;
	}
}

.teachers {
	@include fluidify(margin-top, 24px, 64px);

	&__list {
		grid-column: wide-start / content-end;
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
		grid-gap: var(--base-3);
	}
}