.intro--tutorial {
    grid-column: full-start / full-end;
    @include block-layout-reset;
    align-content: start;
    padding: var(--gutter) 0;
}

.tutorial-intro-title {
    margin-top: 0;
}

.tutorial-intro-desc {
    font-family: var(--font-stack);
    font-weight: 400;
    max-width: 40ch;
}

.tutorials-list {
    counter-reset: tutorial-counter;
    list-style: none;
    padding: 0;
    display: grid;
    row-gap: var(--base-4);

    &--no-counter .tutorial-preview::before {
        display: none;
    }

    &__item {
        counter-increment: tutorial-counter;
    }
}

.tutorial-latest-preview {
    position: relative;
    padding: var(--base-4);

    &:hover {
        .tutorial-preview__right .link--gradient-inversed {
            @include gradient;
            @include gradient-stuff;
        }

        .tutorial-preview__left::after {
            opacity: 0.5;
        }
    }

    @media screen and (max-width: $breakpoint-sm) {
        padding: 0;
    }

    .tutorial-preview__left {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .tutorial-preview__right {
        margin-right: auto;
        padding: var(--base-4);
        max-width: 40ch;
        border-radius: 20px;
        background-color: hsla(265, 24%, 7%, 0.9);

        @supports (-webkit-backdrop-filter: blur(0px)) or (backdrop-filter: blur(0px)) {
            -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
            background-color: hsla(265, 24%, 7%, 0.6);
        }

        @media screen and (max-width: $breakpoint-sm) {
            margin-right: 0;
            max-width: initial;
        }
    }

    .tutorial-preview__image {
        @media screen and (max-width: $breakpoint-sm) {
            height: 100%;
            width: auto;
        }
    }
}

.tutorial-preview {
    display: grid;
    grid-template-columns: 450px 1fr;
    gap: var(--base-2);
    position: relative;

    &::before {
        content: counter(tutorial-counter);
        position: absolute;
        top: var(--base-2);
        left: var(--base-2);
        height: 4ch;
        width: 4ch;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background-color: var(--clr-shade-1);
        color: white;
        border: 2px solid white;
        z-index: 1;
        pointer-events: none;
        font-weight: 400;

        @supports (-webkit-backdrop-filter: blur(0px)) or (backdrop-filter: blur(0px)) {
            -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
            background-color: hsla(265, 24%, 7%, 0.5);
        }
    }

    &:hover {
        .tutorial-preview__right .link--gradient-inversed {
            @include gradient;
            @include gradient-stuff;
        }

        .tutorial-preview__left::after {
            opacity: 0.5;
        }
    }

    @media screen and (max-width: $breakpoint-lg) {
        grid-template-columns: 360px 1fr;
    }

    @media screen and (max-width: $breakpoint-sm) {
        grid-template-columns: 1fr;
    }

    &__link {
        position: absolute;
        z-index: 1;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
    }

    &__left {
        height: 300px;
        overflow: hidden;
        background-color: var(--card-bg);
        box-shadow: var(--card-shadow);
        border-radius: 20px;

        @media screen and (max-width: $breakpoint-lg) {
            height: 250px;
        }

        @media screen and (max-width: $breakpoint-sm) {
            height: 60vw;
        }

        &::after {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            content: "";
            @include gradient;
            border-radius: 20px;
            opacity: 0;
            transition: opacity ease-out 0.15s;
        }
    }

    &__left,
    &__right {
        position: relative;
    }

    &__image {
        width: 100%;
        margin-left: 50%;
        transform: translateX(-50%);
        object-fit: contain;
    }

    &__title {
        margin-top: 0;
        margin-bottom: 0;
        line-height: 1.1;
    }

    &__desc {
        margin-top: var(--base);
    }

    &__bottom-link {
        position: relative;

        >a {
            position: relative;
            z-index: 2;
        }
    }
}

.next-tutorials {
    .tutorial-preview--next::before {
        content: "→";
    }

    .tutorial-preview--prev::before {
        content: "←";
    }

    &__heading {
        font-family: var(--font-stack);
        color: var(--text-disabled);
        text-transform: uppercase;
        margin: var(--base-4) 0 var(--base) 0;
    }

    &__grid {
        display: grid;
        gap: var(--base);
    }
}