:root {
	--text-primary-black: black;
	--text-primary-white: white;
	--text-secondary-black: hsla(0, 0%, 0%, 0.6);
	--text-secondary-white: hsla(0, 0%, 100%, 0.65);
	--text-disabled-black: hsla(0, 0%, 0%, 0.35);
	--text-disabled-white: hsla(0, 0%, 100%, 0.4);
	--divider-black: hsla(0, 0%, 0%, 18%);
	--divider-white: hsla(0, 0%, 100%, 24%);

	--clr-shade-1: hsl(265, 24%, 7%);
	--clr-shade-2: hsl(265, 18%, 14%);
	--clr-shade-3: hsl(265, 19%, 21%);
	--clr-shade-4: hsl(265, 14%, 80%);
	--clr-shade-5: hsl(265, 10%, 90%);
	--clr-shade-6: hsl(265, 6%, 96%);

	--clr-red: hsl(0, 80%, 55%);
	--clr-tomato: hsl(12, 85%, 55%);
	--clr-orange: hsl(32, 85%, 55%);
	--clr-yellow: hsl(50, 85%, 55%);
	--clr-lime: hsl(75, 80%, 55%);
	--clr-green: hsl(140, 75%, 55%);
	--clr-cyan: hsl(180, 75%, 47%);
	--clr-azure: hsl(200, 75%, 55%);
	--clr-blue: hsl(220, 75%, 60%);
	--clr-ultramarine: hsl(244, 85%, 60%);
	--clr-purple: hsl(270, 85%, 60%);
	--clr-pink: hsl(330, 80%, 60%);
	--clr-crimson: hsl(348, 80%, 55%);
	--text-primary: var(--text-primary-white);
	--text-secondary: var(--text-secondary-white);
	--text-disabled: var(--text-disabled-white);

	--glob-bg: var(--clr-shade-1);
	--divider: var(--divider-white);
	--card-bg: var(--clr-shade-2);
	--card-bg-always-dark: var(--clr-shade-2);
	--card-shadow: 0 12px 30px hsla(0, 0%, 0%, 0.5);
	--button-shadow: 0 6px 16px hsla(0, 0%, 0%, 0.5);
	--button-border-hover: white;
	--top-grad: linear-gradient(to bottom, var(--clr-shade-2), var(--glob-bg));
	--bottom-grad: linear-gradient(to top, var(--clr-shade-3), var(--glob-bg));
	--error-grad: linear-gradient(to bottom, var(--clr-shade-3), var(--clr-shade-2), var(--glob-bg));
	--error-grad-flipped: linear-gradient(to bottom, var(--clr-shade-3), var(--clr-shade-2), var(--clr-shade-2), var(--glob-bg));

	--clr-primary: var(--clr-tomato);
	--clr-secondary: var(--clr-crimson);

	--gradient: linear-gradient(to right, var(--grad-1) 5%, var(--grad-2) 20%, var(--divider) 100%);
}

$gradient: linear-gradient(to right, var(--grad-1) 5%, var(--grad-2) 20%, var(--divider) 100%);

